
.global-background {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: #eee;
}
.global-background .background-image {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: .8;
    background-image: url("../assets/img/bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.global-background .background-canvas {
    position: absolute;
    width: 100%;
    height: 100%;
}



