
.back-top {
  position: fixed;
  right: 40px;
  bottom: 90px;
  z-index: 20000;
  width: 45px;
  height: 45px;
  text-align: center;
  line-height: 45px;
  border-radius: 50%;
  background-color: #20a0ff;
  color: #fff;
  cursor: pointer;
  opacity: 0.5;
  transition: all 300ms ease;
}
.back-top i {
    font-size: 23px;
}
.back-top:hover {
    opacity: 1;
}



