body {
  font-size: 14px;
  line-height: 1.4;
  color: #333;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#main {
  position: relative;
  padding-top: 80px;
}
#main .main-content {
    float: left;
    width: 42.5em;
    margin: 0 0 0 12.5em;
    position: relative;
    overflow: hidden;
    transition: width 0.35s;
}
.empty-box {
  text-align: center;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



